	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Happy Flaming Advent","date":"04 Dec 2005","slug":"/2005-12-04-happy-flaming-advent/","tags":"Y2005"},"html":"<p><a href=\"http://www.mopo.de/2005/20051203/hamburg/panorama/feuer_beim_nobel_juwelier_und_die_feuerwehr_stand_auf_dem_schlauch.html\" title=\"Hamburger Morgenpost - www.mopo.de - Nachrichten Hamburg Panorama\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/12/mopobecker.png\" border=\"0\" height=\"220\" width=\"158\" alt=\"mopobecker.png\" align=\"right\" /></a> I've <a href=\"http://www.papascott.de/archives/2004/11/09/a-nation-of-pyromaniacs/\" title=\"PapaScott &raquo; Blog Archive &raquo; A Nation of Pyromaniacs\">ranted before</a> about how the fascination with fire in Germany is downright dangerous, now in Hamburg we have a <a href=\"http://www.mopo.de/2005/20051203/hamburg/panorama/feuer_beim_nobel_juwelier_und_die_feuerwehr_stand_auf_dem_schlauch.html\" title=\"Hamburger Morgenpost - www.mopo.de - Nachrichten Hamburg Panorama\"> concrete</a> <a href=\"http://www.abendblatt.de/daten/2005/12/03/509542.html\" title=\"Juwelier ausgebrannt - Millionenschaden\">example</a>:</p>\n<blockquote><p>She forgot to blow out the candle on the Advent wreath. At 8 pm the last employee left the Uhren-Becker jeweler on Ida-Ehre-Platz - 90 minutes later the shop was in flames. It took hours for fire fighters to get through the iron gate and bullet-proof glass protecting the shop and to bring the fire under control. Damage is in the millions.</p></blockquote>\n<p>It's unclear whether insurance will cover the loss. A neighbor of ours works for Becker, he'll be busy the next few weeks tallying the damage.</p>"},"next":{"fields":{"title":"Happy Bionicle Advent","date":"03 Dec 2005","slug":"/2005-12-03-happy-bionicle-advent/"}},"previous":{"fields":{"title":"In Front of the Dakota","date":"04 Dec 2005","slug":"/2005-12-04-in-front-of-the-dakota/"}}},{"node":{"fields":{"title":"Happy Bionicle Advent","date":"03 Dec 2005","slug":"/2005-12-03-happy-bionicle-advent/","tags":"Y2005"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/69774443/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/18/69774443_56087807df.jpg\" width=\"500\" height=\"375\" alt=\"IMG_0473\" /></a></p>\n<p>An Advent scene created by Christopher.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/69774834/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/9/69774834_6cd10b493f.jpg\" width=\"500\" height=\"375\" alt=\"IMG_0472\" /></a></p>"},"next":{"fields":{"title":"Safer Travel","date":"02 Dec 2005","slug":"/2005-12-02-safer-travel/"}},"previous":{"fields":{"title":"Happy Flaming Advent","date":"04 Dec 2005","slug":"/2005-12-04-happy-flaming-advent/"}}},{"node":{"fields":{"title":"Safer Travel","date":"02 Dec 2005","slug":"/2005-12-02-safer-travel/","tags":"Y2005"},"html":"<p><a href=\"http://www.wired.com/news/privacy/0,1848,69712,00.html\">Bruce Schneier on airport security</a>. \"Exactly two things have made airline travel safer since 9/11: reinforcement of cockpit doors, and passengers who now know that they may have to fight back. Everything else...is security theater.\" (quote lifted from <a href=\"http://www.kottke.org/remainder/05/12/9941.html\">kottke.org</a>)</p>"},"next":{"fields":{"title":"Frozen Food","date":"01 Dec 2005","slug":"/2005-12-01-frozen-food/"}},"previous":{"fields":{"title":"Happy Bionicle Advent","date":"03 Dec 2005","slug":"/2005-12-03-happy-bionicle-advent/"}}}],"pathPrefix":"","first":false,"last":false,"index":262,"pageCount":1101}}

